import { createObj } from "../fable_modules/fable-library.3.7.1/Util.js";
import { singleton } from "../fable_modules/fable-library.3.7.1/List.js";

export function updateRecordStateProperty(getFn, setFn, key, newVal, table) {
    const r = createObj(singleton([key, newVal]));
    table._obj.setOptions((prev) => {
        const newState = { ...getFn(prev.state), ...r };
        setFn(prev.state, newState);
        return { ...prev, ...table._obj.options, onStateChange: (() => {
        }) };
    });
    return table;
}

export function updateStateFunctionCall(fn, table) {
    fn(table._obj);
    table._obj.setOptions((prev) => ({ ...prev, ...table._obj.options, onStateChange: (() => {
    }) }));
    return table;
}

